import request from '@/utils/testRequest'
//获取统计的答卷信息
export function getAnswerPaperRecord(data){
  return request({
    url: '/otpApi/business/record/getAnswerPaperRecord?pageNum=' + data.pageNum +'&pageSize=' + data.pageSize,
    method:'POST',
    data: data
  })
}
//获取信息
export function getStatisKpInfo(query){
  return request({
    url: '/otpApi/business/record/getStatisKpInfo/'+query.aprId,
    method:'get',
    data: query
  })
}
//试卷
export function queryTreeNoAllSubjectSelect(query) {
  return request({
    url: '/otpApi/business/subject/queryTreeNoAllSubjectSelect',
    method: 'get',
    data: query
  })
}
//平均分的接口
export function getAnswerPaperGpa(data) {
  return request({
    url: '/otpApi/business/record/getAnswerPaperGpa?pageNum=' + data.pageNum +'&pageSize=' + data.pageSize,
    method: 'POST',
    data: data
  })
}
// 用户继续答题
export function continueAnswerQuestions(data) {
	return request({
		url: '/otpApi/business/testPaper/getTPAnswerRecordByUserId?userId='+data.userId+'&paperId='+data.paperId,
		method: 'post',
		data
	})
}
// 新增考试记录
export function CreateAnswerPaperRecord(data) {
	return request({
		url: '/otpApi/business/record/saveAnswerPaperRecord',
		method: 'post',
		data
	})
}
// 新增答题记录
export function CreateAnswerRecord(data) {
	return request({
		url: '/otpApi/business/aqrecord/addAnsQuesRecordResultAnswer',
		method: 'post',
		data
	})
}
// 提交考试记录
export function submitAnswerPaperRecord(data) {
	return request({
		url: '/otpApi/business/record/submitAnswerPaperRecord',
		method: 'post',
		data
	})
}
// 获取考试试卷详情
export function GetTestPaperDetail(query) {
	return request({
		url: '/otpApi/business/testPaper/getTestPaperExamInfoById?paperId='+query.paperId+'&ansRecordId='+query.ansRecordId,
		method: 'get',
		data:query
	})
}